




























































































































@import '../styles/variables.scss';

.projects {
    width: 100%;
    display: flex;
    position: relative;
    padding: calc(#{$navHeight + 40}) 0;

    .animatedLetters {
        @include fontXXL;
        text-align: left;
        margin-bottom: 40px;
    }

    ul {
        width: 100%;
        display: grid;
        grid-template: 300px / repeat(auto-fit,minmax(200px,1fr));
        grid-column-gap: 4%;
        overflow: hidden;
    }

    li {
        color: $white;

        &:hover figure:before {
            opacity: .2;
        }
    }

    figure {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;

        &:before {
            content: attr(text);
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            color: $white;
            background: $black;
            opacity: 0;
            z-index: 1;
        }

        img {
            position: relative;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

    }

}

